ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 78px;
}

li {
    float: left;
}

li a {
  display: block;
  text-decoration: none;
  min-width: 118px;
  line-height: 80px;
  text-align: center;
  color: #4F4F4F;
  font-weight: bold;
  padding: 0 15px 0 15px;
}

li a:hover {
    background-color: #154569;
    color: white;
}

.active {
    background-color: #1F6193;
    color: white;
}

.about-dropdown {
  display: none;
  position: absolute;
  background-color: #154569;
  z-index: 1;
  color: white;
  margin-top: -5px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  right: 0;
}

/* Show the dropdown menu on hover */
.about-care:hover .about-dropdown {display: -ms-flexbox;display: flex;}
.about-care:hover .about-btn {
    background-color: #154569;
    color: white;
}

.dropdown-button {
  width: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 20px;
  -ms-flex-positive: 2;
      flex-grow: 2;
}

.dropdown-label {
 -ms-flex-item-align: center;
     align-self: center; 
}

.dropdown-button:hover {
  background-color: #1F6193;
}

.mobile-nav {
  display: none; 
}

.burger-label {
  display: inline-block; 
  font-size: 22px;
  padding-bottom: 5px;
  margin-left: 10px;
}

.mobile-nav-menu {
  top: 170px;
  width:100%;
  height:100%;
  background-color: #154569;
  z-index: 99;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.mobile-nav-item:active {
  background-color: #1F6193;
}

.mobile-nav-item {
  font-size: 22px;
  color: white;
  border-bottom: 1px solid #1F6193;
  padding-left: 30px;
  padding-top: 30px;
  padding-bottom: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.burger-row {
  width: 40%;
  text-decoration: none;
  display: none;
  padding: 15px;
}

.mobile-search-row {
  color: black;
  width: 40%;
  text-decoration: none;
  display: none;
  padding: 15px;
}

.right-chevron {
  height: 26px;
  float: right;
  margin-right: 20px;
}

.left-chevron {
  height: 26px;
  float: left;
  margin-right: 20px;
}

.mobile-nav-icon {
  margin-right: 10px;
}

.super-container {
  height: 100vh;
  background-color: #154569;
}

.mobile-nav-menu a {
  text-decoration: none;
}

.desktop-nav {
  cursor: pointer;
  position: relative;
}

@media only screen and (max-width: 1000px) { 

  .burger-row {
    display: inline-block;
    -ms-flex-line-pack: center;
        align-content: center;
    text-decoration: none;
  }
  .mobile-search-row {
    display: inline-block;
    -ms-flex-line-pack: center;
        align-content: center;
    text-decoration: none;
  }


  .desktop-nav {
    display: none; 
  }

  .mobile-nav {
    /* display: flex; */
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .mobile-nav2 {
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-align: start; 
        align-items: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    min-height: 100vh;
  }
}




